import React, { useEffect, useState } from "react";
import { calendar__bttom, noImage } from "../../../assets/images";
import { i18n } from "../../../i18n";
import Breadcrumb from "../Breadcrumb";
import Image from "../Image";
import "./DetaillWrapper.css";
import { useRouteMatch } from "react-router-dom";
import actions from "src/modules/publication/list/publicationListActions";
import selectors from "src/modules/publication/list/publicationListSelectors";
import { useDispatch, useSelector } from "react-redux";
import Translate from "../Translate";
import Date from "../Date";
import { Link } from "react-router-dom";
import { baseUrl } from "../../shared/BaseUrl";
import PublicationService from "src/modules/publication/publicationService";

function Detaill() {
  const match = useRouteMatch();
  const id = match.params.id;
  const dispatch = useDispatch();
  // const rows = useSelector(selectors.selectRows);
  const [rows, setRows] = useState({});
  const loading = useSelector(selectors.selectLoading);
  const language = localStorage.getItem("language");
  const doFetch = async () => {
    if (match.path === "/detaill/category/:id") {
      const response = await PublicationService.findbygroup(id);
      setRows(response);
      // dispatch(actions.doFindAllByCategory(id));
    } else if (match.path === "/detaill/:id") {
      const response = await PublicationService.findbythematique(id);
      // dispatch(actions.doFindAllByThematique(id));
      setRows(response);
    }
  };
  let titleBreadcrumb =
    match.path === "/detaill/category/:id"
      ? Translate.Trans("title", rows)
      : Translate.Trans("title", rows);

  useEffect(() => {
    doFetch();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [dispatch, id]);

  return (
    <div className="app__detaill">
      {loading && (
        <h4 className="h4 detaill__sketlon">{i18n("table.loading")}</h4>
      )}
      {/* {!selectHasRows && <Empty />} */}
      {!loading && rows && (
        <>
          <div className="app__calendar">
            <Link to="/evenement">
              <img src={calendar__bttom} alt={noImage} />
            </Link>
          </div>
          <div className="__detaillwrapper">
            <Breadcrumb
              title={titleBreadcrumb}
              items={[
                [i18n("app.title"), "/"],
                [i18n(titleBreadcrumb), "/publication"],
              ]}
            />

            <div className="detaill">
              <div className="list__detaills">
                {Object(rows)?.publications?.map((item) => (
                  <Link to={`/detail/${item.id}`}>
                    <div>
                      {language === "fr" ? (
                        <Image
                          src={
                            item.photos.length > 0
                              ? `${baseUrl}/file/download?privateUrl=${item.photos[0]?.privateUrl}`
                              : noImage
                          }
                          alt={noImage}
                          id="image__desktop"
                          width="370"
                          height="300"
                        />
                      ) : language === "ar" ? (
                        <Image
                          src={
                            item.photosAR.length > 0
                              ? `${baseUrl}/file/download?privateUrl=${item.photosAR[0]?.privateUrl}`
                              : noImage
                          }
                          alt={noImage}
                          id="image__desktop"
                          width="370"
                          height="300"
                        />
                      ) : (
                        <Image
                          src={
                            item.photosEN.length > 0
                              ? `${baseUrl}/file/download?privateUrl=${item.photosEN[0]?.privateUrl}`
                              : noImage
                          }
                          alt={noImage}
                          id="image__desktop"
                          width="370"
                          height="300"
                        />
                      )}

                      <div className="list__detaill">
                        <div className="detaill__header">
                          <div></div>
                          <div className="header__right">
                            {" "}
                            {Date.HourMinute(item.date)}
                          </div>
                        </div>{" "}
                        <div
                          className="detaill__content text__wrap"
                          style={{ maxWidth: 350 }}
                        >
                          {Translate.Trans("title", item)}
                        </div>
                      </div>
                    </div>
                  </Link>
                ))}
              </div>
            </div>
          </div>
        </>
      )}
    </div>
  );
}

export default Detaill;
